import React from "react";
import { useTranslation } from "react-i18next";
import { FaPlus } from "react-icons/fa";
import { Button } from "reactstrap";
import { useHistory } from "react-router-dom";
import UploadFileWrapper from "components/common/UploadFile/UploadFileWrapper";
import "./PageHeader.css";

export default function PageHeader({ headerTitle, addPageUrl }) {
    const { t } = useTranslation();

    const history = useHistory();

    const handleAddOnClick = function (e) {
        e.preventDefault();
        history.push(addPageUrl);
    };

    return (
        <div className="page-header">
            <div className="d-flex flex-wrap justify-content-between">
                <h1>{headerTitle.toUpperCase()}</h1>
                <div className="align-self-center btn_group_wrapper">
                    <Button
                        color="success"
                        onClick={handleAddOnClick}
                        className="btn_add_subject"
                    >
                        <FaPlus /> {t("admin.common.ADD")}
                    </Button>
                    <div className="upload_file_btn_wrapper">
                        <UploadFileWrapper />
                    </div>
                </div>
            </div>
        </div>
    );
}
