export function exportHead() {
  const head = [];
  head.push("Code");
  head.push("Vietnamese Name");
  head.push("Englist Name");
  head.push("Number Of Credits");
  head.push("Number Of Theoretical Periods");
  head.push("Number Of Practice Periods");
  head.push("Number Of Self-study Periods");
  return head;
}

export function exportRow(data) {
  const row = [];
  row.push(data.ma);
  row.push(data.tenTiengViet);
  row.push(data.tenTiengAnh);
  row.push(data.soTinChi);
  row.push(data.soTietLyThuyet);
  row.push(data.soTietThucHanh);
  row.push(data.soTietTuHoc);
  return row;
}
