import React, { useState } from "react";
import { toast } from "react-toastify";
import { ReactExcel, readFile } from "@ramonak/react-excel";
import "./UploadFile.css";
import monHocApi from "api/common/monHoc.api";
import i18n from "i18n/i18n";

const UploadFile = (props) => {
    const [file, setFile] = useState(null);
    const [fileUploaded, setFileUploaded] = useState(null);
    const [currentSheet, setCurrentSheet] = useState({});
    const [errorDuplicate, setErrorDuplicate] = useState('');

    const onFileUpload = async () => {
        setErrorDuplicate('');
        if (file) {
            const result = await monHocApi.import(file);
            if (!result?.isError) {
                toast.success("IMPORT_SUCCESSFULLY");
                props?.setIsOpen(false);
                return;
            }
            if(result?.data?.list){
                const listDup = result?.data?.list || [];
                const errorStr = listDup?.map(item => item?.ma)?.join(', ');
                setErrorDuplicate(errorStr)
            }
            toast.error(`${result?.message || 'ERROR'}`);
        }
    };
    const handleUpload = (event) => {
        setErrorDuplicate('');
        const file = event.target.files[0];
        setFile(file);
        //read excel file
        readFile(file)
            .then((fileUpload) => {
                setFileUploaded(fileUpload);
            })
            // eslint-disable-next-line
            .catch((error) => console.log(error));
    };
    return (
        <div className="upload_file_content">
            <div className="upload_file_top">
                <div className="form_upload">
                    <input
                        type="file"
                        name="file"
                        id="fileXlsx"
                        className="fileXlsx"
                        accept=".xlsx"
                        onChange={handleUpload}
                        hidden
                    />
                    <label for="fileXlsx" className="actual-btn">
                        Choose File
                    </label>
                    <span id="file-chosen">
                        {file?.name || i18n.t("NO_FILE_CHOSEN")}
                    </span>
                </div>
                {file?.name && (
                    <div className="btn_submit_file">
                        <button onClick={onFileUpload}>Submit</button>
                    </div>
                )}
            </div>
            {errorDuplicate ? <div className='error_duplicate'>
                <p>Danh sách mã bị trùng: {errorDuplicate || ''}</p>
            </div> : ''}
            <div className="preview_xlsx">
                <ReactExcel
                    initialData={fileUploaded}
                    onSheetUpdate={(currentSheet) =>
                        setCurrentSheet(currentSheet)
                    }
                    activeSheetClassName="active-sheet"
                    reactExcelClassName="react-excel"
                />
            </div>
        </div>
    );
};

export default UploadFile;
